@use "mixins" as *;

.mainSection {
  display: flex;
  flex-direction: column;
  height: 100vh;
  width: calc(100vw - 400px);
}
.subMenu {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh;
  max-height: 920px;
  width: 400px;
}
.header {
  align-items: center;
  display: flex;
  flex-direction: row;
  height: 90px;
  justify-content: flex-end;
  width: 100%;
}
.mainContent {
  padding-right: 10px;
  display: flex;
  flex-direction: column;
  height: calc(100% - 90px);
  width: 100%;
}

@include IntermediateView {
  .mainSection {
    width: 100vw;
  }
  .subMenu {
    position: absolute;
    height: calc(100% - 80px);
    bottom: 0px;
    right: 0px;
    background-attachment: fixed;
    background-image: url(../assets/background.webp);
    background-position: center top;
    background-size: cover;
    width: 100%;
    max-width: 400px;
    z-index: 15;
    // display: none;
  }
}

@include TabletView {
  .header {
    height: 70px;
  }
  .subMenu {
    height: calc(100% - 60px);
  }
  .mainContent {
    height: calc(100% - 70px);
  }
}
