@use "../variables" as *;
@use "../mixins" as *;

.searchBar {
  margin-right: 50px;
  padding: 10px;
  background: rgba(196, 196, 196, 0.3);
  border-radius: 15px;
  display: flex;
  align-items: center;
  &:hover {
    background: rgba(196, 196, 196, 0.5);
  }
}
.searchField {
  width: 300px;
  background-color: transparent;
  border: none;
  font-family: Poppins;
  font-style: normal;
  font-weight: 600;
  font-size: $D_subContentFontSize;
  color: rgb(255, 255, 255);
  &::placeholder {
    color: rgba(255, 255, 255, 0.5);
  }
  &:focus {
    outline: none;
  }
}
.searchButton {
  background-color: transparent;
  border: none;
  &:hover {
    transform: scale(1.2);
  }
  &:focus {
    outline: none;
  }
}

@include TabletView {
  .searchBar {
    margin-right: 15px;
    padding: 5px;
    border-radius: 10px;
  }
  .searchField {
    transition: width 100ms cubic-bezier(0, 0.51, 1, 0.59);
    width: calc(100vw - 140px);
  }
}

@include MobileView {
  .searchField {
    transition: width 20ms cubic-bezier(0, 0.51, 1, 0.59);
  }
}
